.App {
  text-align: center;
}

.App-header {
  background-color: #128C7E; /* Similar to WhatsApp green */
  padding: 20px;
  color: white;
}

.chat-container {
  width: 400px; /* Adjust the width of the chat box */
  height: 500px; /* Adjust the height of the chat box */
  border: 1px solid #ccc;
  border-radius: 10px;
  margin: 50px auto;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.messages {
  flex: 1;
  padding: 10px;
  overflow-y: auto;
}

.message {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.from-user .message {
  justify-content: flex-start;
}

.from-bot .message {
  justify-content: flex-end;
}

.icon {
  margin-right: 10px;
}

.text {
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 10px;
}

.form-container {
  display: flex;
  align-items: center;
  padding: 10px;
}

input[type="text"] {
  flex: 1;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

button[type="submit"] {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

button[type="submit"]:focus {
  outline: none;
}

button[type="submit"]:hover svg {
  fill: #128C7E; /* Change color on hover */
}

button[type="submit"] svg {
  width: 20px;
  height: 20px;
  fill: #555; /* Icon color */
}

.loading {
  margin-top: 10px;
  font-style: italic;
  color: #999;
}
